import React, { useState } from 'react';
import { Icon, Menu, Input, Segment } from 'semantic-ui-react'
import { Link } from "react-router-dom";

function Footer() {

    return (
        <Menu vertical>
          <Menu.Item href='//example.com' target='_blank'>
            Visit another website
          </Menu.Item>
          <Menu.Item link>Link via prop</Menu.Item>
          <Menu.Item onClick={this.handleClick}>Javascript Link</Menu.Item>
        <Menu.Menu position='right'>
          <Menu.Item>
            <Icon name='search' size='large' color='blue' />
          </Menu.Item>
          <Menu.Item>
            <Icon name='calendar' size='large' color='blue' />
          </Menu.Item>
          <Menu.Item>
            <Icon name='heart' size='large' color='blue' />
          </Menu.Item>
          <Menu.Item>
            <Icon name='user circle' color='blue' size='large' />
          </Menu.Item>
          <Menu.Item
            name='logout' color='blue'
          />
        </Menu.Menu>
      </Menu>
    )
  }

export default Footer;
